.dropzone {
color: #000000;
border: 2px solid #000000;
padding: 25px;
display: inline-block;
margin: 15px 0;
border-radius: 5px;
}


.align-items-end {
  display: flex;
  align-items: flex-end;
}
.form-sec {
   padding-bottom: 50px;
}
.form-sec .notification {
	border: 1px solid #e5e5e5;
	padding: 7px 20px 7px 8px;
  border-radius: 4px;
  display: flex;
}
.form-sec .customer-sec {
	border: 1px solid #e5e5e5;
	padding: 8px 0 8px 8px;
	border-radius: 4px;
}
.form-sec .physicians-sec{
	border: 1px solid #e5e5e5;
	padding: 8px 0 8px 8px;
	border-radius: 4px;
}
.form-sec .studies-table .st_heaing {
	font-size: 12px;
    font-family: Helvetica;
    color: #000000;
    line-height: 1.2;
    font-weight: 400;
	padding: 0 8px;
	border-right: 1px solid #000;
}

.form-sec .studies-table .st-data {
	font-size: 12px;
    font-family: Helvetica;
    color: #000000;
    line-height: 1.2;
    font-weight: 400;
  padding: 0 18px;
  border: 1px solid #000;
}
.form-sec .studies-table tr th:nth-child(5) {
  padding-right: 130px;
}
.studies-table {
  width: 100%;
  border: 1px solid;
}
.studies-table tr {
  border-bottom: 1px solid;
}
.report-text .studies-table {
  width: 82%;
}
.form-sec .access_number .st-data {
  padding: 0 25px;
}
.form-sec .studies-table tr th:last-child {
  padding-right: 130px;
}
.form-sec .studies-table tr td:nth-child(5) {
  padding-right: 10px;
}
.form-sec .studies {
	border: 1px solid #e5e5e5;
	padding: 8px;
	border-radius: 4px;
}
.form-sec .notification {
	border: 1px solid #e5e5e5;
	padding: 10px;
	border-radius: 4px;
}
.notification .form-check-inline {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.form-sec .notification .study-bt {
	display: flex;
	align-items: flex-end;
}
.form-sec .report-text {
  margin-bottom: 10px;
  position: relative;
}
.form-sec .report-btn {
	float: right;
	line-height: 34px;
  padding: 0 25px;
  position: absolute;
  top:8px;
  right:10px;
}

.form-sec .report-text .form-control {
  width: 82%;
}
.error-color {
  color: red
}








.form-sec  label {
	font-size: 12px;
    font-family: Helvetica;
    color: rgb(153, 153, 153);
    line-height: 1.2;
	margin-bottom: 5px;
}
.form-sec .form-check-label {
	color: rgba(0, 0, 0, 0.7);
}
.form-sec .notification  label {
	margin-bottom: 0;
}
.form-sec .form-control {
	padding: 6px;
	color: rgb(0, 0, 0);
}
.form-sec .form-control::placeholder {
	font-size: 16px;
    font-family: Helvetica;
    color: rgb(0, 0, 0);
    line-height: 1.2;
}
.form-sec span {
	font-size: 12px;
    font-family: Helvetica;
    color: rgb(153, 153, 153);
    line-height: 1.2;
}
/*--From Right Sec*/
.form-sec .from-right-sec {
	margin-top: 60px;
}

.phy-btns-row {
  display: flex;
  justify-content: flex-end;
  padding: 15px 10px;
}
